.button {
    display: inline-block;
    padding: 0px 40px;
    font-size: 24px;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    outline: none;
    color: #fff;
    background-color: #4CAF50;
    border: none;
    border-radius: 15px;
    box-shadow: 0 9px #999;
    margin-bottom: 15px;
    width: 50%;
    max-width: 445px;
}

@media (max-width: 700px) {
    .button {
        width: 70%;
    }
}

.button:hover {
    background-color: #3e8e41
}

.button:active {
    background-color: #3e8e41;
    box-shadow: 0 5px #666;
    transform: translateY(4px);
}

h4 {
    margin: auto;
}
