.fDiv {
    background-color: rgb(255, 255,255);
}
.fDivdark{
    background-color: rgb(0, 0, 0);
}

.head {
    padding: 0;
    margin: 0;
    display: flex;
    position: sticky;
    top: 0;
    left: 0;
}

.AppLogo {
    max-width: 100%;
}

.body {
    background-color: rgba(255, 255, 255, 0.9);
    color: black;
    position: absolute;
    width: 100%;
    text-align: center;
    justify-content: center;
    display: block;

}
.bodyDark {
    background-color: rgba(0, 0, 0, 0.9);
    color: white;
    position: absolute;
    width: 100%;
    text-align: center;
    justify-content: center;
    display: block;
}

.ps {
    color: black;
    padding-bottom: 30px;
    padding-top: 25px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    box-sizing: border-box;
    background-color: #a3cea4;
}
.location {
    display: flex;
    font-family: 'Roboto', sans-serif;
    flex-direction: column;
    position: relative;
    font-weight: bold;
    align-items: center;
    width: 33.3%;
}
.socials {
    color: black;
    display: flex;
    font-family: 'Roboto', sans-serif;
    flex-direction: column;
    position: relative;
    font-weight: bold;
    align-items: center;
    width: 33.3%;
}
.icons {
    align-items: center;
    display: flex;
}
.ico{
    cursor: pointer;
}

.last {
    height: 100%;
}

