.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.About-enter {
  transform: translateX(350px);
}
.About-enter-active {
  transform: translateX(0px);
  transition: all 500ms ease-in;
}
.About-exit {
  opacity: 1;
}
.About-exit-active {
  transform: translateX(-350px);
  transition: all 500ms ease-in;
}
